@media (max-width: 1100px) {
  //header media queries
  .header__inner {
    .nav__menu {
      grid-template-columns: repeat(auto-fit,minmax(45px, 1fr)) !important; }
    .nav__personalAccount {
      margin-left: 60px; }
    .btnBlock a:not(:last-of-type) {
      margin-right: 10px !important; } }
  .home-page {
    height: auto;
    min-height: auto; } }



@media (max-width: 900px) {
  //header media queries
  .header__inner {
    padding-bottom: 70px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    position: relative;
    .logo {
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      img {
        width: 150px; } }

    .nav {
      box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);
      transition: right .3s linear;
      padding: 0;
      background: #fff;
      height: 100%;
      position: fixed;
      right: -200px;
      width: 100%;
      max-width: 200px;
      justify-content: flex-start;
      display: flex;
      flex-direction: column;
      align-items: center;
      z-index: 100;
      .nav__personalAccount {
        transition: background-color .6s linear;
        .account {
          margin: 0;
          border: none;
          padding: 0;
          width: 100%;
          height: 100%;
          .account__link {
            padding: 0;
            transition: color .6s linear; }
          &:hover .account__link {
            font-weight: normal;
            color: #BC9CFF;
            text-shadow: none; } } }

      .nav__personalAccount:hover .account__link {
        font-weight: normal;
        color: black; }
      .nav__menu {
        width: 100%;
        margin-top: 45px;
        display: flex;
        flex-direction: column;
        .nav__link {
          display: block;
          padding: 10px 0;
          width: 100%;
          text-align: center;
          transition: background .6s linear;
          &:hover {
            background: #BC9CFF;
            color: white; } } }
      .nav__personalAccount {
        width: 100%;
        padding: 10px 0;
        margin: 0;
        display: flex;
        justify-content: center;
        align-items: center; } }
    .nav--opened {
      right: 0; }
    .nav--logined {
      flex-direction: column-reverse;
      justify-content: flex-end;
      .nav__menu {
        margin-top: 0; }
      .nav__personalAccount {
        margin-top: 45px;
        border-bottom: none; } }
    .hamburgerMenu {
      z-index: 101;
      display: block;
      position: fixed;
      right: 0;
      top: 17px; }
    .hamburgerMenu__inner {
      display: flex; } }
  .nav__link--active {
    background: #BC9CFF !important;
    color: white !important; }
  //btn media
  .btnBlock {
    width: 100%;
    display: flex;
    justify-content: space-evenly; }
  .header__inner .nav__personalAccount .btnBlock a:not(:last-of-type) {
    margin: 0; }
  .btn {
    font-size: 10px;
    padding: 6px 16px; }
  //footer
  .footer__inner {
    grid-template-columns: repeat(4,1fr); }
  .footer__item:last-of-type {

    grid-column-start: 1;
    grid-column-end: 5;
    display: flex;
    justify-content: center; }
  .footer__subscribe {
    padding: 20px;
    border: 2px solid #BC9CFF;
    border-radius: 12px;
    max-width: 280px; }
  .footer__item .logo {
    width: 100px; }
  .footer__description {
    font-size: 10px;
    line-height: 14px; }
  .footer__title {
    font-size: 8px; }
  .footer__link {
    font-size: 10px;
    margin-top: 5px; }
  .footer__input {
    padding: 2px 10px; }

  .room-page__inner {
    display: flex;
    flex-direction: column; }
  .filter {
    display: flex;
    align-content: center;
    flex-wrap: wrap;
    justify-content: space-around; }
  .filter__item {
    padding: 5px 10px;
    min-width: 265px; }
  .single-room__inner {
    display: flex;
    flex-direction: column;
    align-items: center; }
  //.payment
  .payment__hotel-room {
    font-size: 19px;
    .payment__lux {
      font-size: 14px; } }
  .payment__cost {
    font-size: 14px;
    .payment__per-day {
      font-size: 19px; } } }


@media (max-width: 600px) {
  .footer__inner {
    grid-template-columns: 2fr repeat(3,1fr); }
  .single-room__description {
    display: flex;
    flex-direction: column; }
  .details__item {
    max-width: 100%; }
  .social {
    max-height: 20px; }
  .social-icon:not(:last-of-type) {
    margin-right: 3px; }
  .social-icon img {
    width: 15px;
    height: 15px; }
  .page-404__title {
    font-size: 1rem; } }


@media (max-width: 350px) {
  .footer__item .logo {
    width: 80px; }
  .footer__description:not(:first-of-type) {
    font-size: 8px;
    line-height: 14px; }
  .footer__link {
    font-size: 8px;
    margin-top: 2px; } }

@media (max-height: 320px) {
  .nav--opened {
    overflow: scroll; } }
